.card{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgb(82, 56, 56);

  margin: 30px auto;
  width: 250px;
  height: 250px; 
  border-radius: 20px;
  box-shadow: 2px 0px 5px 2px rgba(0,0,0,0.25), -5px 0px 5px 0px rgba(0,0,0,0.22);
  cursor: pointer;
  transition: 0.4s;
  color: rgb(210, 210, 210);
}

.card:hover {
  transform: scale(1.2, 1.2);
  box-shadow: 2px 2px 10px 5px rgba(0,0,0,0.25), 
    -5px -5px 10px 0px rgba(0,0,0,0.22);
}

.cards{
  display: flex;
  flex-wrap: wrap;
}

.name{
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.A{
  stroke: #00FF00;
}

.B{
  stroke: #CCFF00;
}

.C{
  stroke: #FFCC33;
}

.D{
  stroke: #FF9900;
}

.E{
  stroke: #FF3366;
}

.F{
  stroke: #FF0000;
}

.circular-chart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 250px;
}

.circle-bg {
  fill: none;
  stroke: #eee;
  stroke-width: 2.8;
}

.circle {
  fill: none;
  stroke-width: 1.8;
  stroke-linecap: round;
  animation: progress 1s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.percentage {
  font-size: 8px;
  text-anchor: middle;
  stroke-width: 0.5;
  font-weight: lighter;
}